.wrapper {
  margin: auto;
  width: 50%;
}

.text-box {
  border-style: solid;
  border-width: 5px;
  text-align: center;
  white-space: pre-wrap;
  margin-bottom: 10px;
  padding: 5%;
  width: 100%;
}

.flex-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.flex-container-row {
  display: flex;
  flex-direction: row;
}

form {
  width: 100%;
  margin: 10px;
}

form button {
  align-self: center;
}

.table-container {
  display: table;
  width: 100%;
  margin: 10px;
}

.row {
  display: table-row;
}

.cell {
  display: table-cell;
}

.row label {
  text-align: right;
  padding-right: 1%;
}

.flex-container-row button {
  margin: 0px 5px 20px 0px;
}